<template>
  <div class="forgot-password">
    <div class="card">
      <button @click="$emit('change-page', 'login')" class="back-btn">
        ‚Üê ËøîÂõûÁôªÂΩï
      </button>
      
      <h2 class="title">ÂøòËÆ∞ÂØÜÁ†Å</h2>
      <p class="subtitle">ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊ≥®ÂÜåÈÇÆÁÆ±ÔºåÊàë‰ª¨Â∞ÜÂèëÈÄÅÈ™åËØÅÁ†Å</p>

      <form @submit.prevent="handleResetPassword">
        <div class="form-group">
          <label>ÈÇÆÁÆ±</label>
          <div class="input-wrapper">
            <span class="icon">üìß</span>
            <input
              type="email"
              v-model="form.email"
              placeholder="ËØ∑ËæìÂÖ•Ê≥®ÂÜåÈÇÆÁÆ±"
              required
              :disabled="codeSent"
            />
          </div>
        </div>

        <!-- ÂèëÈÄÅÈ™åËØÅÁ†ÅÊåâÈíÆ -->
        <button
          v-if="!codeSent"
          type="button"
          @click="handleSendCode"
          class="send-code-btn"
        >
          ÂèëÈÄÅÈ™åËØÅÁ†Å
        </button>

        <!-- È™åËØÅÁ†ÅÂíåÊñ∞ÂØÜÁ†ÅËæìÂÖ•Ê°ÜÔºàÂèëÈÄÅÈ™åËØÅÁ†ÅÂêéÊòæÁ§∫Ôºâ -->
        <template v-if="codeSent">
          <div class="form-group">
            <label>È™åËØÅÁ†Å</label>
            <div class="input-wrapper">
              <span class="icon">üîë</span>
              <input
                type="text"
                v-model="form.code"
                placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
                required
                maxlength="6"
              />
            </div>
          </div>

          <div class="form-group">
            <label>Êñ∞ÂØÜÁ†Å</label>
            <div class="input-wrapper">
              <span class="icon">üîí</span>
              <input
                :type="showPassword ? 'text' : 'password'"
                v-model="form.newPassword"
                placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†ÅÔºàËá≥Â∞ë6‰ΩçÔºâ"
                required
                minlength="6"
              />
              <button
                type="button"
                @click="showPassword = !showPassword"
                class="eye-btn"
              >
                {{ showPassword ? 'üëÅÔ∏è' : 'üëÅÔ∏è‚Äçüó®Ô∏è' }}
              </button>
            </div>
          </div>

          <button type="submit" class="submit-btn">ÈáçÁΩÆÂØÜÁ†Å</button>
        </template>

        <!-- Ê∂àÊÅØÊèêÁ§∫ -->
        <div v-if="message.text" :class="['message', message.type]">
          {{ message.text }}
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ForgotPassword',
  emits: ['change-page'],
  data() {
    return {
      showPassword: false,
      codeSent: false,
      verificationCode: '',
      form: {
        email: '',
        code: '',
        newPassword: ''
      },
      message: {
        type: '',
        text: ''
      }
    }
  },
  methods: {
    handleSendCode() {
      if (!this.form.email) {
        this.message = { type: 'error', text: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±' }
        return
      }

      // ÁîüÊàê6‰ΩçÈöèÊú∫È™åËØÅÁ†Å
      this.verificationCode = Math.floor(100000 + Math.random() * 900000).toString()
      this.codeSent = true
      
      // Ê®°ÊãüÂèëÈÄÅÈ™åËØÅÁ†ÅÔºàÂÆûÈôÖÈ°πÁõÆ‰∏≠Â∫îËØ•Ë∞ÉÁî®ÂêéÁ´ØAPIÔºâ
      this.message = { 
        type: 'success', 
        text: `È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÂà∞ ${this.form.email}ÔºàÊ®°ÊãüÈ™åËØÅÁ†Å: ${this.verificationCode}Ôºâ` 
      }

      console.log('ÂèëÈÄÅÈ™åËØÅÁ†Å:', this.verificationCode)
    },

    handleResetPassword() {
      if (!this.form.code || !this.form.newPassword) {
        this.message = { type: 'error', text: 'ËØ∑Â°´ÂÜôÂÆåÊï¥‰ø°ÊÅØ' }
        return
      }

      if (this.form.newPassword.length < 6) {
        this.message = { type: 'error', text: 'ÂØÜÁ†ÅËá≥Â∞ëÈúÄË¶Å6‰Ωç' }
        return
      }

      if (this.form.code !== this.verificationCode) {
        this.message = { type: 'error', text: 'È™åËØÅÁ†ÅÈîôËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•' }
        return
      }

      // ËøôÈáåÂèØ‰ª•Ë∞ÉÁî®ÂÆûÈôÖÁöÑÈáçÁΩÆÂØÜÁ†Å API
      this.message = { type: 'success', text: 'ÂØÜÁ†ÅÈáçÁΩÆÊàêÂäüÔºÅÂç≥Â∞ÜË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢...' }
      
      console.log('ÈáçÁΩÆÂØÜÁ†ÅÊàêÂäü')

      // 2ÁßíÂêéË∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
      setTimeout(() => {
        this.$emit('change-page', 'login')
      }, 1500)
    }
  }
}
</script>

